.slidercontainer{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
}
.slider {
    display: inline-flex;
    justify-content: flex-start;
    position: relative;
    width: 100vw;
    height: 80vh;
    flex-direction: column;
    background-color: #ffffff;
    border-radius: 3vh 3vh 0px 0px;
    align-items: center;
    transition: 0.3s;
}

.handler {
    width: 100vw;
    height: 11vh;
    position: relative;
    background-color: #ffffff;
    border-radius: 3vh 3vh 0px 0px;
}

.slider .slider-vertical {
    position: relative;
    width: 100vw;
    height: 11vh;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.slider .slider-vertical .div-2 {
    position: absolute;
    width: 15vw;
    height: 1vh;
    background-color: black;
    border-radius: 20px;
}

.slider .div-3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    align-self: stretch;
    width: 100%;
    flex: 0 0 auto;
    justify-content: space-evenly;
}

.slider .txt-web {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2vh;
    padding: 0px 0px 4vh;
    position: relative;
    align-self: stretch;
    width: 100%;
    flex: 0 0 auto;
}

.slider .title {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  align-self: stretch;
  width: 100%;
  flex: 0 0 auto;
}

.slider .author {
    position: relative;
    align-self: stretch;
    color: black;
    font-size: 2vh;
    margin-left: 3vw;
    margin-right: 3vw;
    margin-top: 0;
    margin-bottom: 0;
    text-align: center;
    font-family: "TT Norms Pro-Regular", Helvetica;
}

.slider .name {
    position: relative;
    align-self: stretch;
    margin-top: 0;
    margin-right: 3vw;
    margin-left: 3vw;
    margin-bottom: 0;
    font-family: "TT Norms Pro-Regular", Helvetica;
    color: black;
    font-size: 2vh;
    font-weight: bold;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
}

.slider .text-wrapper {
    position: relative;
    align-self: stretch;
    margin-top: 2vh;
    margin-right: 3vw;
    margin-left: 3vw;
    margin-bottom: 0;
    font-family: "TT Norms Pro-Regular", Helvetica;
    color: black;
    font-size: 1.5vh;
    font-weight: lighter;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
}

.slider .line {
  position: relative;
  align-self: center;
  width: 150%;
  height: 0.7vh;
  background-color: black;
  box-shadow: 0px 3px 4px 0px #99999983;
}

.slider .apps-title-logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 100px;
    position: relative;
    align-self: stretch;
    width: 100%;
    flex: 0 0 auto;
}

.slider .text-wrapper-2 {
    position: relative;
    align-self: stretch;
    font-family: "TT Norms Pro-ExtraBold", Helvetica;
    font-weight: bold;
    color: black;
    font-size: 1.8vh;
    padding: 2vh;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
}

.slider .apps {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    align-self: stretch;
    width: 100%;
    flex: 0 0 auto;
}

.slider .app-store {
    position: absolute;
    width: inherit;
    height: inherit;
    background-color: black;
    border-radius: 100px;
    border: none
}

.slider .googlebutton {
    display: flex;
    width: 50vw;
    height: 15vw;
    align-items: center;
    gap: 5vw;
    justify-content: flex-start;
    flex-direction: row;
    margin-bottom: 3vh;
    border: none
}

.slider .applebutton {
    display: flex;
    width: 50vw;
    height: 15vw;
    align-items: center;
    gap: 5vw;
    justify-content: flex-start;
    flex-direction: row;
    margin-bottom: 3vh;
    border: none
}


.slider .icon {
    position: relative;
    width: 12vw;
    height: 12vw;
    border: 0.6vw solid;
    border-color: aliceblue;
    border-radius: 12vw;
    display: flex;
    margin: 1.2vw;
    justify-content: center;
    align-items: center;
}

.slider .android {
    position: relative;
    width: 4vh;
    height: 4vh;
    background-image: url(../../../public/images/android.png);
    background-size: cover;
    background-position: center;
}

.slider .ios {
    position: relative;
    width: 4vh;
    height: 4vh;
    background-image: url(../../../public/images/ios.png);
    background-size: cover;
    background-position: center;
}

.slider .text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    justify-content: center;
}

.slider .text-wrapper-3 {
    position: relative;
    margin-top: -1px;
    font-family: "Raleway", Helvetica;
    color: #ffffff;
    font-size: 3vw;
    letter-spacing: 0;
    line-height: normal;
    white-space: nowrap;
}

.slider .text-wrapper-4 {
    position: relative;
    font-family: "Raleway", Helvetica;
    color: #ffffff;
    font-size: 4vw;
    font-weight: bold;
    letter-spacing: 0;
    line-height: normal;
    white-space: nowrap;
}

.slider .RZD-logo-st-neg-cmyk {
  background-image: url(../../../public/images/rzdLogoStNegCmyk1.png);
  background-size: cover;
  background-position: 50% 50%;
  position: relative;
  width: 30vw;
  height: 9vh;
  margin-bottom: 1vh;
}